.social-media {
  margin-bottom: 20px;
  text-align: center;

  .social-share {
    margin-bottom: 20px; }

  h4 {
    font-size: rem(30px);
    font-family: default-font(300);
    color: $c0; }

  img {
    width: 100%;
    height: auto; }

  .facebook, .instagram, .twitter {
    margin: 0 10px;
    display: block; }

  .social-item {
    height: 80px;
    width: 80px;
    fill: $c0; }

  .hashtag {
    display: block;
    color: $c0;
    font-size: rem(28px);
    font-family: default-font(300); } }
