.sign-me-up {
  margin: 30px auto;
  text-align: center;

  h1 {
    margin: 0 auto 40px;
    text-align: center;
    font-size: rem(30px);
    font-family: default-font(500);
    max-width: 600px;
    color: $c0; }

  img {
    width: 100%;
    height: auto; }

  .sign-me-up-btn {
    padding: 10px 50px;
    border: 0;
    border-radius: 60px;
    background: $c0;
    font-size: rem(36px);
    color: $c7;
    font-family: default-font(300);
    text-transform: uppercase; } }

