.call-to-action {
  margin: 30px auto;

  img.screenshots-img {
    position: relative;
    z-index: 1;
    max-width: 100%;
    height: auto; }

  .screenshots {
    position: relative;
    @include desktop {
      width: 63%; }

    &::before {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      content: '';
      height: 100%;
      width: 75%;
      border-radius: 280px;
      background: rgba($c1, 0.2);
      filter: blur(50px); }

    h3 {
      color: $c0;
      font-family: default-font(700);
      text-align: center;
      font-size: rem(18px); } } }
